import { css, modifier, styled } from '@lokalise/styled';

import { color, radius } from '../../utils/theme';

export const Container = styled.span`
	display: flex;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: center;
	padding: 0 0 0 1px;
`;

interface ButtonInterface {
	selected: boolean;
}

export const Button = styled.button<ButtonInterface>`
	position: relative;
	display: inline-block;
	padding: 4px 7px 8px;
	height: 30px;
	z-index: 1;
	vertical-align: middle;
	margin: 0 0 0 -1px;

	background: transparent;
	border: 1px solid ${color('primary.300')};
	font-size: 20px;
	color: ${color('interactive.primary.idle')};

	${modifier(
		'selected',
		css`
			z-index: 2;

			background: ${color('surface.primary50')};
			border-color: ${color('interactive.primary.idle')};
		`,
	)}

	&:first-child {
		border-radius: ${radius('md')} 0 0 ${radius('md')};
	}
	&:last-of-type {
		border-radius: 0 ${radius('md')} ${radius('md')} 0;
	}

	&:hover,
	&:focus {
		color: ${color('interactive.primary.hover')};
		border-color: ${color('interactive.primary.hover')};

		${modifier(
			'selected',
			css`
				background: ${color('surface.primary50')};
			`,
		)}
	}

	&:active {
		fill: ${color('interactive.primary.pressed')};
		color: ${color('interactive.primary.pressed')};
		border-color: ${color('interactive.primary.pressed')};
		background: ${color('surface.primary100')};
	}
`;
